@import '../../styles/mixins/Mixins.module';

.wrapper {
  @include wrapper
}

.welcome-note {
  margin-top: 40px;
  font-size: 2rem;
  display: block;
}

.dog {
  width: 40%;
  margin: 30px auto;
  display: block;
}

@media only screen and (max-width: 1100px) {
  .welcome-note {
    font-size: 1.8rem;
  }
}

@media only screen and (max-width: 1000px) {
  .welcome-note {
    font-size: 1.3rem;
  }
}

@media only screen and (max-width: 700px) {
  .welcome-note {
    font-size: 1rem;
  }
}



