@import '../../styles/mixins/Mixins.module';
@import '../../styles/colors/Colors.module';

.wrapper {
  @include wrapper
}

.dog-element {
  display: inline-block;
  width: 30%;
  margin: 10px;
}

.dog-img {
  margin: 10px auto;
  border-radius: 5px;
  display: block;
  width: auto;
  max-width: 100%;
  height: 220px;
}

.delete-button {
  @include button ($black, $dogify-btn, $dogify-btn, $black);
}

.delete-button .delete-text {
  display: none;
}

.delete-button:hover .trash-icon {
  display: none;
}

.delete-button:hover .delete-text {
  display: inline;
}

.fade-away {
  animation: fade-away 0.5s linear;
}

@keyframes fade-away {
  from {opacity: 1}
  to {opacity: 0}
}


