@import '../../styles/mixins/Mixins.module';
@import '../../styles/colors/Colors.module';

.wrapper {
  @include wrapper
}

.dog-img {
  height: 220px;
  margin: 10px auto;
  border-radius: 5px;
  display: block;
  max-width: 90%;
}

.dog-element {
  display: inline-block;
  width: 30%;
  margin: 10px;
}

.add-button {
  @include button ($dark-navy, $white, $white, $dark-navy, $dark-navy);
}

.add-icon {
  font-size: 0.95rem;
}

.add-button .add-text {
  display: none;
}

.add-button:hover .add-icon {
  display: none;
}

.add-button:hover .add-text {
  display: inline;
}

.dogified .add-icon {
  display: none;
}

.dogified {
  @include button ($dogify-btn, $black);
  pointer-events: none;
}

.dogs-wrapper {
  animation: animate-images 1s ease;
  margin: 0 auto;
}

@keyframes animate-images {
  from { opacity: 0; }
  to { opacity: 1; }
}

.img-wrapper {
  position: relative;
}

.rotate {
  @include rotate;
  animation: spin 1s ease;
  transition: all 1s;
}


