@import '../../../styles/colors/Colors.module';

.logo {
  display: block;
  width: 50%;
  margin: 30px auto 0;
}

.navigation {
  background: $main-bg;
  width: 30%;
  float: left;
  height: 100vh;
  position: fixed;
  font: {
    size: 5rem;
    weight: bold;
    family: GoodDog, Arial, sans-serif;
  }
  text-align: center;
}

.menu {
  width: 70%;
  margin: 15% auto;
  cursor: pointer;
  list-style: none;
  padding: 0;
  font: {
    size: 1.8rem;
    family: Monospace, Arial, sans-serif;
  }

}

.item, .active-item {
  margin: 30px 0;
  padding: 10px 0;
  border-radius: 5px;
  text-align: left;

  &:hover {
    background: $white;
    transition: all 0.6s;
    opacity: 0.8;
  }
}

.active-item {
  font-weight: bold;
  text-decoration: underline;
}

.invisible {
  visibility: hidden;
  display: none;
}

.img-no-label {
  background: $black;
  color: $main-bg;
  border-radius: 4px;
  font-size: 1.5rem;
  display: inline;
  padding: 3px 5px;
  margin-left: 10px;
}

.menu-icon {
  display: inline;
}

@media only screen and (max-width: 600px) {
  .navigation {
    font-size: 3rem;
  }

  .menu {
    font-size: 1.1rem;
    width: 90%;
  }

  .img-no-label {
    font-size: 1rem;
  }
}

@media only screen and (min-width: 600px) {
  .navigation {
    font-size: 3.5rem;
  }

  .menu {
    font-size: 1.4rem;
    width: 90%;
  }

  .img-no-label {
    font-size: 1.2rem;
  }
}

@media only screen and (min-width: 750px) {
  .navigation {
    font-size: 4.5rem;
  }

  .menu {
    font-size: 1.7rem;
    width: 90%;
  }

  .img-no-label {
    font-size: 1.4rem;
  }
}

@media only screen and (min-width: 992px) {
  .navigation {
    font-size: 5rem;
  }

  .menu {
    font-size: 1.8rem;
    width: 70%;
  }

  .img-no-label {
    font-size: 1.5rem;
  }
}


