@import '../../../styles/mixins/Mixins.module';
@import '../../../styles/colors/Colors.module';

.wrapper {
  margin: 20px 0;
}

.active {
  background: #43616d;
  display: inline-block;
  margin: 0 5px;
  font: {
    size: 1rem;
    weight: bold;
  }
  cursor: pointer;
  padding: 5px 10px;
  color: $white;
  border: none;
  border-radius: 4px;
  &:focus {
    outline: 0;
  }
  &:hover {
    background: #17202A;
    color: #F7DC6F;
  }
}

.pages-button {
  display: inline-block;
  margin: 0 3px;
  font: {
    size: 1rem;
    weight: bold;
  }
  cursor: pointer;
  width: 30px;
  height: 30px;
  background: $add-btn;
  color: $white;
  border: none;
  border-radius: 100%;
  transition: all 0.6s;
  &:focus {
    outline: 0;
  }
  &:hover {
    background: $dogify-btn;
    color: $add-btn;
  }
}

.images-amount-button {
  display: inline-block;
  margin: 0 3px;
  font: {
    size: 1rem;
    weight: bold;
  }
  cursor: pointer;
  width: 30px;
  height: 30px;
  background: $dark-navy;
  color: $white;
  border: none;
  border-radius: 100%;
  transition: all 0.6s;
  &:focus {
    outline: 0;
  }
  &:hover {
    background: $dogify-btn;
    color: $add-btn;
  }
}

.images-amount {
  margin-top: 50px;
  position: absolute;
  right: 50px;
}

.images-on-page {
  display: inline-block;
  font: {
    size: 1.3rem;
    weight: bold;
  }
  color: $black ;
}



