@import '../../styles/mixins/Mixins.module';
@import '../../styles/colors/Colors.module';

.wrapper {
  @include wrapper
}

.dog-element {
  height: 60vh;
}

.dog-img {
  display: block;
  margin: 10px auto;
  border-radius: 5px;
  height: 95%;
}

.add-button {
  @include button ($dark-navy, $white, $white, $dark-navy, $dark-navy);
}

.add-icon {
  font-size: 0.95rem;
}

.add-button .add-text {
  display: none;
}

.add-button:hover .add-icon {
  display: none;
}

.add-button:hover .add-text {
  display: inline;
}

.dogified .add-icon {
  display: none;
}

.dogified {
  @include button ($dogify-btn, $black);
  pointer-events: none;
}

.dog-element {
  margin: 20px auto 0;
  display: block;
}
.no-display {
  display: none;
}

.dog-wrapper {
  animation: animate-image 1.5s;
}

@keyframes animate-image {
  from {opacity: 0} to {opacity: 1}
}

.rotate {
    @include rotate;
    animation: spin 1s ease;
    transition: all 1s;
}


