.heading {
  text-align: center;
  font: {
    family: "GoodDog", Arial, sans-serif;
    size: 5rem;
  }
  letter-spacing: 4px;
  display: inline-block;
}

.logo-img {
  width: 65px;
}

.logo {
  display: inline-block;
  margin-left: 20px;
}

.wrapper {
  width: 100%;
  margin: 15px auto;
}

@media only screen and (max-width: 1100px) {
  .heading {
    font-size: 4rem;
  }
  .logo-img {
    width: 55px;
  }
}

@media only screen and (max-width: 870px) {
  .heading {
    font-size: 3.5rem;
  }
  .logo-img {
    width: 45px;
  }
}

@media only screen and (max-width: 650px) {
  .heading {
    font-size: 3rem;
  }
  .logo-img {
    width: 40px;
  }
  .logo {
    margin-left: 10px;
  }
}

@media only screen and (max-width: 570px) {
  .heading {
    font-size: 2.5rem;
  }
  .logo-img {
    width: 35px;
  }
  .logo {
    margin-left: 10px;
  }
}


