@mixin wrapper {
  text-align: center;
  width: 69%;
  float: right
}

@mixin button ($bg:'', $color:'', $hover-bg:'', $hover-color:'', $hover-border:'') {
  display: inline-block;
  font: {
    size: 1rem;
    weight: bold;
  }
  cursor: pointer;
  padding: 12px;
  background: $bg;
  color: $color;
  border: 1px solid $bg;
  border-radius: 4px;
  transition: all 0.6s;
  &:focus {
    outline: 0;
  }
  &:hover {
    background: $hover-bg;
    color: $hover-color;
    border: 1px, solid, $hover-border;
  }
}

@mixin rotate {
  @keyframes spin {
    0% {
      border-radius: 5px;
    }

    50% {
      border-radius: 50%;
      opacity: 0.5;
    }

    100% {
      transform: rotate(360deg);
      transform-origin: center center;
      border-radius: 5px;
      opacity: 1;
    }
  }
}


